<template>
    <div v-if="lan=='TW'">
        <h2>客戶端 指南</h2>
        <b-card id="Setting" title="設定">
            <div>
                <p>路徑: 個人頁面 => 設定</p>
                <p>
                    偏好設定的位置 <br>
                    在此調整你的顏色主題, 介面語言
                </p>
            </div>
        </b-card>
        <b-card id="CreateProject" title="建立專案">
            <div>
                <p>
                    建立專案涉及權限 <br>
                    用戶必須在某個擁有建立專案權限的組織元素下, 才會看到此頁面
                </p>
                <p>工具列上的</p>
                <b-img fluid :src="ImageURL('img/ClientDocs/AddCreateNav.png')" /> <br><br>
                <b-card title="創建專案的資料結構">
                    <ul>
                        <li>專案名稱: 新增專案的名稱</li>
                        <li>預算: 專案估計的預算</li>
                        <li>初始金額: 初始獲得的金額</li>
                        <li>開始日期: 開工日期</li>
                        <li>簽約日期: 與客戶簽約的日期</li>
                        <li>群組: 是否加入已存在專案群組</li>
                        <li>群組清單: 專案群組清單</li>
                    </ul>
                </b-card>
            </div>
        </b-card>
        <b-card id="AddProject" title="加入專案">
            <div>
                <p>
                    任何帳號都有加入專案的權限 <br>
                    但是在加入專案之前要先取得專案的邀請碼
                </p>
                <p>工具列上的</p>
                <b-img fluid :src="ImageURL('img/ClientDocs/AddCreateNav.png')" /> <br><br>
                <b-card title="加入專案的資料結構">
                    <ul>
                        <li>邀請碼: 指定專案的邀請碼, 伺服器會尋找指定專案讓用戶加入</li>
                    </ul>
                </b-card>
            </div>
        </b-card>
        <b-card id="CreateSchedule" title="建立行程表 (任務群組)">
            <div>
                <p>
                    在建立任務之前, 必須要有任務群組 <br>
                    任務群組主要用於分類使用, 每個任務群組會指向一個部門 (執行單位) <br>
                </p>
                <p>路徑: 專案 => 頻道標題 => 行程表 => 主要任務</p>
                <b-img fluid :src="ImageURL('img/ClientDocs/ScheduleMenu.png')" /> <br><br>
                <b-card title="任務群組的資料結構">
                    <ul>
                        <li>標題: 任務群組的名稱</li>
                        <li>人力: 配置人力, 這個會影響成本與圖像顯示</li>
                        <li>敘述: 這個行程的敘述</li>
                        <li>開始日期: 這個行程開始的日情</li>
                        <li>部門: 執行單位</li>
                    </ul>
                </b-card>
            </div>
        </b-card>
        <b-card id="CreateTask" title="建立任務">
            <div>
                <p>路徑: 專案 => 頻道標題 => 行程表 => 子任務</p>
                <p>
                    任務是主要的作業, 一個任務可以註冊複數的任務群組 <br>
                    每個任務群組的註冊都必須標註評估時間
                </p>
                <b-card title="任務的資料結構">
                    <ul>
                        <li>標題: 任務的標題</li>
                        <li>內容: 任務的內容</li>
                        <li>圖片: 附加的圖片</li>
                        <li>檔案: 附加的檔案</li>
                        <li>流程: 註冊的任務群組</li>
                    </ul>
                </b-card>
            </div>
        </b-card>
        <b-card id="TaskLifeCycle" title="任務系統生命週期">
            <div>
                <p>
                    專案的核心就是任務流程的管理 <br>
                    任務執行的生命週期的主要有四點
                </p>
                <ul>
                    <li>行程表</li>
                    <li>部門的任務頻道</li>
                    <li>個人行為</li>
                    <li>審查清單</li>
                </ul>
                <b-card title="行程表">
                    專案管理員會在行程表的部分進行任務的派工動作 <br>
                    將職員要執行的作業安排好
                </b-card>
                <b-card title="部門的任務頻道">
                    根據時間與部門, 任務的項目會出現在部門的任務頻道內 <br>
                    這個時候職員可以在頻道接下任務
                </b-card>
                <b-card title="個人行為">
                    個人行為會出現用戶所選擇的任務 <br>
                    上面會出現已經過的時間 <br>
                    用戶可以結束, 完結, 取消目前的行為 <br>
                    <ol>
                        <li> 結束: 尚未完成, 告一段落 </li>
                        <li> 完結: 已完成, 任務送審查清單 </li>
                        <li> 取消: 刪除行為, 不會保留紀錄 </li>
                    </ol>
                </b-card>
                <b-card title="審查清單">
                    完成的任務會顯示在上面 <br>
                    等待專案管理員檢查 <br>
                    檢查完畢後有兩種完結模式可以選擇<br>
                    <ol>
                        <li>接受: 任務的完成度滿意, 開始生成考績資料</li>
                        <li>回絕: 任務的完成度不滿意, 任務流程不會往前, 會通知職員回絕的消息</li>
                    </ol>
                </b-card>
            </div>
        </b-card>
        <b-card id="Request" title="審查">
        </b-card>
    </div>
    <div v-else>
        <h2>Client Guide</h2>
        <b-card id="Setting" title="Setting">
            <div>
                <p>Path: Personal => Setting</p>
                <p>
                    Appearance position <br>
                    You can adjust your language setting, theme setting
                </p>
            </div>
        </b-card>
        <b-card id="CreateProject" title="Create Project">
            <div>
                <p>
                    You need permission to create proejct <br>
                    User must have create project permission option to be able to see create project page
                </p>
                <p>On navigation bar</p>
                <b-img fluid :src="ImageURL('img/ClientDocs/AddCreateNav.png')" /> <br><br>
                <b-card title="Create project data structure">
                    <ul>
                        <li>Project name: Name of the project</li>
                        <li>Budget: Approximation budget</li>
                        <li>Initial: Initial budget</li>
                        <li>Start day: Execute day</li>
                        <li>Sign day: The day sign with client</li>
                        <li>IsGroup: Join exist project group</li>
                        <li>Groups: Project group list</li>
                    </ul>
                </b-card>
            </div>
        </b-card>
        <b-card id="AddProject" title="Join project">
            <div>
                <p>
                    Any account can join project <br>
                    But project invite code is need it
                </p>
                <p>On navigation bar</p>
                <b-img fluid :src="ImageURL('img/ClientDocs/AddCreateNav.png')" /> <br><br>
                <b-card title="Join project data structure">
                    <ul>
                        <li>Invite code: Project invite code, Server will search target project for join action</li>
                    </ul>
                </b-card>
            </div>
        </b-card>
        <b-card id="CreateSchedule" title="Create schedule (Task container)">
            <div>
                <p>
                    Before create task, We must have task container <br>
                    Task container is for categorise, each container will targeting a department (Executive unit)
                </p>
                <p>Path: Project => Channel title => Schedule => Main Task</p>
                <b-img fluid :src="ImageURL('img/ClientDocs/ScheduleMenu.png')" /> <br><br>
                <b-card title="Schedule data structure">
                    <ul>
                        <li>Title: Task container name</li>
                        <li>Manpower: Manpower, Will effect budget calculate and graph</li>
                        <li>Description: Task info</li>
                        <li>Start day: Schedule start day</li>
                        <li>Department: Executive unit</li>
                    </ul>
                </b-card>
            </div>
        </b-card>
        <b-card id="CreateTask" title="Create Task">
            <div>
                <p>Path: Project => Channel title => Schedule => Sub Task</p>
                <p>
                    Task is the core of the application <br>
                    A task can have multiple targeting task container <br>
                    Each targeting must have estimate time
                </p>
                <b-card title="Task data structure">
                    <ul>
                        <li>Ttile: Task title</li>
                        <li>Content: Task content</li>
                        <li>Image: Additional images</li>
                        <li>File: Additional files</li>
                        <li>Stage: Execute order</li>
                    </ul>
                </b-card>
            </div>
        </b-card>
        <b-card id="TaskLifeCycle" title="Task Life Cycle">
            <div>
                <p>
                    The core concept of project is task management <br>
                    There are four stage for task life
                </p>
                <ul>
                    <li>Schedule</li>
                    <li>Department task channel</li>
                    <li>Personal activity</li>
                    <li>Request</li>
                </ul>
                <b-card title="Schedule">
                    Project manager will setup the schedule that staff will execute then in the future
                </b-card>
                <b-card title="Department task channel">
                    Depending on time and department, the quest will appear in department quest channel <br>
                    Staff can take the quest then start working
                </b-card>
                <b-card title="Personal activity">
                    Personal activity will show current taking quest <br>
                    It will show the passing time <br>
                    Staff can end, finish, cancel the activity <br>
                    <ol>
                        <li> End: Not finish the quest yet </li>
                        <li> Finish: Sending the request channel </li>
                        <li> Cancel: Delete the activity, it won't leave any record </li>
                    </ol>
                </b-card>
                <b-card title="Request">
                    Finish quest will list <br>
                    Waiting project manager to reviwe <br>
                    There are two option to choose after you finish the review<br>
                    <ol>
                        <li>Accept: Satisfied the execute result, Spawn performance appraisal</li>
                        <li>Reject: Unsatisfied the execute result, It won't moving forward, and notice the working staff</li>
                    </ol>
                </b-card>
            </div>
        </b-card>
        <b-card id="Request" title="Request">
        </b-card>
    </div>
</template>

<script>
import Base from '../../../Base'
import main from '../../../../main'
export default Base.extend({
    name: "ClientGuide",
    props:{
        lan: String
    },
    computed:{
        main(){
            return main;
        }
    },
})
</script>
