import Vue from 'vue'
import VueRouter from 'vue-router'
import Page from '../Page.vue'

Vue.use(VueRouter)

export default new VueRouter({
    mode: 'hash',
    routes: [
      { path: '/', redirect: '/Home' },
      {
        path: '/:topic',
        name: "Topic",
        component: Page
      },
      {
        path: '/:topic/:page',
        name: "Page",
        component: Page
      },
      {
        path: '/:topic/:page/:index',
        name: "Index",
        component: Page
      }
    ]
  })