import Base from './Base'
import { bus } from '../main';

export default Base.extend({
  name: 'Nav',
  props:{
    lan: String
  },
  methods: {
    changeLan(tag){
      console.log("Change to: " + tag);
      bus.$emit("ChangeLan", tag);
    },
    changeTopic(v){
      document.location.href = this.Tohref(v, undefined, undefined)
    }
  }
})