<template>
    <div v-if="lan=='TW'">
        <h2>客戶端 專案頻道</h2>
        <b-card id="Info" title="專案簡介">
            <p>
                專案的用途在於集中分類管理工作, 資源, 外包, 人力, 訊息傳遞, 變更管理 <br>
                App 專案的功能將以頻道的方式進行分類 <br>
                不同的頻道有不同的功能
            </p>
            <b-img fluid :src="ImageURL('img/ClientDocs/ProjectInfoTW.png')" /> <br><br>
            <p>
                當進入工具列上的專案後, 頻道清會更新專案的頻道清單 <br>
            </p>
            <ol>
                <li>通訊: 大廳文字通訊交流頻道</li>
                <li>檔案: 專案下的檔案管理, Nas 支援</li>
                <li>審查: 部門送出完成的派工清單</li>
                <li>評估: 成本評估, 帳本管理</li>
                <li>視覺化: 將專案的資料視覺化呈現</li>
                <li>外包: 處理工作項目的外包, 管理外包商的資訊</li>
                <li>部門: 動態生成的部門頻道, 部門子頻道包括 (通訊, 任務, 論壇)</li>
            </ol>
        </b-card>
        <b-card id="Setting" title="專案設定">
            <p>
                專案設定的地方 <br>
                從專案的大標題找下拉選單進入
            </p>
            <b-img fluid :src="ImageURL('img/ClientDocs/ProjectTitleContextMenuTW.png')" /> <br><br>
            <b-img fluid :src="ImageURL('img/ClientDocs/ProjectSettingTW.png')" /> <br><br>
            <ol>
                <li>專案: 專案的訊息</li>
                <li>部門: 部門集管理</li>
                <li>職員: 參與職員管理</li>
                <li>角色: 權限集管理</li>
                <li>邀請碼: 加入邀請碼管理</li>
            </ol>
            <b-card title="專案">
                <p>
                    專案的標頭資訊, 類似像專案簡介或是大綱的資訊
                </p>
                <p>
                    預設為觀看模式, 點擊 <b-icon icon="pencil-fill" /> 切換編輯模式 <br>
                    當設定更新後, 伺服器會嘗試更新所有人的工具列資訊 (如果員工有加入), 專案訊息 (如果員工在專案的頁面上)
                </p>
                <b-card title="專案的資料結構">
                    <ul>
                        <li>專案名稱: 標題</li>
                        <li>專案副名稱: 外語名稱</li>
                        <li>敘述: 敘述內容</li>
                        <li>預算: 專案總預算, 會影響評估項目的數字計算</li>
                        <li>起始金: 起始金額</li>
                        <li>簽約日: 與客戶的簽約日期</li>
                        <li>開始時間: 專案的開工時間</li>
                        <li>結束時間: 如果為 NAN 代表專案尚未完成</li>
                    </ul>
                </b-card>
            </b-card>
            <b-card title="部門">
                <p>
                    統整人力的集合單位
                </p>
                <b-link @click="To('ClientAPI', 'ClientAPIXlsx', 'Group')">匯入資料表格式</b-link>
                <b-card title="部門的資料結構">
                    <ul>
                        <li>名稱: 部門的標題</li>
                        <li>敘述: 對於部門核心的內容介紹</li>
                        <li>負責人: 統整部門內的人, 在部門頻道中有最高權限</li>
                        <li>副負責人: 統整部門內的第二人, 在部門頻道中有最高權限</li>
                    </ul>
                </b-card>
                <b-alert show>
                    <h4>部門權限</h4> 
                    部門上所掛載的權限意義也有觀看權限的意思,
                    員工要是所擁有的權限集合裡面沒有任何一個在部門權限清單中,
                    那麼這個部門將會被隱藏
                </b-alert>
            </b-card>
            <b-card title="職員">
                <p>
                    參與專案的人員清單
                </p>
            </b-card>
            <b-card title="角色">
                <p>
                    專案下的權限統合單位
                </p>
                <b-link @click="To('ClientAPI', 'ClientAPIXlsx', 'Role')">匯入資料表格式</b-link>
                <b-card title="角色的資料結構">
                    <ul>
                        <li>名稱: 角色的標題</li>
                        <li>顏色: 方便識別</li>
                        <li>專案權限: 專案管理等級的權限清單</li>
                        <li>部門權限: 部門管理等級的權限清單</li>
                    </ul>
                </b-card>
                <b-alert show>
                    <h4>專案權限</h4> 
                    員工的存取能力, 在於員工所獲得的所有角色專案權限加總後的結果
                </b-alert>
                <b-alert show>
                    <h4>部門權限</h4> 
                    員工在部門中的存取能力, 在於員工所獲得的所有角色部門權限加總後的結果
                </b-alert>
            </b-card>
            <b-card title="邀請碼">
                <p>
                    給予未參與專案的職員加入的管道 <br>
                    加入專案時輸入的代碼
                </p>
                <b-alert show>
                    <h4>邀請碼更新</h4> 
                    伺服器每天的 0:00 AM 會更新一次 <br>
                    會將過期的邀請碼刪除掉
                </b-alert>
            </b-card>
        </b-card>
        <b-card id="Schedule" title="專案派工">
            <p>
                在專案的底下新增行程與任務
            </p>
            <p>
                專案設定的地方 <br>
                從專案的大標題找下拉選單進入
            </p>
            <b-img fluid :src="ImageURL('img/ClientDocs/ProjectTitleContextMenuTW.png')" /> <br><br>
            <b-img fluid :src="ImageURL('img/ClientDocs/ScheduleMenu.png')" /> <br><br>
            <b-card title="行程表">
                <p>
                    綁定部門, 提供子任務一個分類的容器
                </p>
                <b-card title="行程表的資料結構">
                    <ul>
                        <li>名稱: 行程表的標題</li>
                        <li>人力: 配給人力, 影響評估與視覺化</li>
                        <li>敘述: 容器敘述</li>
                        <li>開始時間: 行程的開始時間</li>
                        <li>綁定部門: 執行單位</li>
                    </ul>
                </b-card>
                <b-alert show variant="warning">
                    <h4>部門刪除</h4> 
                    部門刪除後, 部門的頻道資料會消失, 但是行程表的資料會保留住 <br>
                    但是會顯示回紅色, 表示沒有綁定部門 <br>
                    這個狀態下在做評估時會直接忽略 (不列入計算範圍)
                </b-alert>
            </b-card>
            <b-card title="子任務">
                <p>
                    敘述工作事項, 綁定複數行程表, 進而得知部門工作順序 <br>
                    在新增前, 必須這個專案中要至少有一項行程表存在
                </p>
                <b-card title="子任務的資料結構">
                    <ul>
                        <li>名稱: 子任務的標題</li>
                        <li>內文: 工作敘述</li>
                        <li>圖片: 附加圖片</li>
                        <li>檔案: 附加檔案</li>
                        <li>綁定行程: 行程表的指標, 同時也綁定部門</li>
                        <li>綁定時間: 部門的執行時間評估</li>
                    </ul>
                </b-card>
                <b-alert show variant="warning">
                    <h4>行程表刪除</h4> 
                    當行程表被刪除時, 任務不會隨之刪除, 不過任務會處於綁定不到行程表的狀態 <br>
                    在 UI 上會呈現紅色狀態 <br>
                    這個狀態下在做評估時會直接忽略 (不列入計算範圍)
                </b-alert>
                <b-alert show variant="danger">
                    <h4>任務刪除</h4> 
                    當任務被刪除時, 任務下綁定的工作紀錄, 審查紀錄, 活動紀錄會一致被刪除掉
                </b-alert>
            </b-card>
        </b-card>
        <b-card id="Text" title="文字頻道">
            <p>
                文字通訊的頻道
            </p>
            <p>
                左下角 <b-icon icon="plus" /> 為送出複數圖片功能
            </p>
            <b-img fluid :src="ImageURL('img/ClientDocs/ModifyTextMessageTW.png')" />
            <p>
                你能修改或刪除已傳輸出去的自己訊息 <br>
                如果你有該部門或是專案的權限, 你能刪除任意頻道內的訊息
            </p>
        </b-card>
        <b-card id="File" title="檔案頻道">
            <h5>主要元素:</h5>
            <ul>
                <li>實體 Nas 檔案總管</li>
                <li>過濾器結構總管</li>
            </ul>
            <h5>敘述:</h5>
            <p>
                在實體 Nas 上的檔案的管理可以透過過濾器結構的設置 <br>
                達到快速存取與過濾的動作
            </p>
            <h5>開啟 Nas 功能</h5>
            <p>
                從檔案頻道的設定開啟 Nas 功能
            </p>
            <b-img fluid :src="ImageURL('img/ClientDocs/EnableNasTW.png')" />
            <h5>設置 Nas 位置</h5>
            <p>
                點進 Nas Editor 後可以新增 Nas 的位置 <br>
                之後進入專案時會自動偵測 Nas 連結
            </p>
            <ul>
                <li>Label: Nas 使用的槽標籤</li>
                <li>Address: IP位址</li>
                <li>Root: 根目錄</li>
            </ul>
            <h5>實體檔案總管</h5>
            <h5>過濾器結構管理</h5>
        </b-card>
        <b-card id="Request" title="審查頻道">
            
        </b-card>
        <b-card id="Evaluation" title="評估頻道">
            
        </b-card>
        <b-card id="Graph" title="視覺化頻道">
            
        </b-card>
        <b-card id="Outsource" title="外包頻道">
            
        </b-card>
        <b-card id="Task" title="任務頻道">
            
        </b-card>
        <b-card id="Issue" title="論壇頻道">
            
        </b-card>
    </div>
    <div v-else>
        <h2>Client Project channel</h2>
        <b-card id="Info" title="Info">
            <b-img fluid :src="ImageURL('img/ClientDocs/ProjectInfoEN.png')" /> <br><br>
            <p>
                When enter the project you select on the naviation bar, Channel list will switch to project channel list <br>
            </p>
            <ol>
                <li>Text: Lobby communicate channel</li>
                <li>File: Project file management, Nas support</li>
                <li>Request: Department finish work</li>
                <li>Evaluation: Cost evaluation, Bill management</li>
                <li>Graph: Project information visualization</li>
                <li>Outsource: Handle oursource job, Manage vendor info</li>
                <li>Department: Dynamic generate department channel, children channel include (Text, Task, Issue)</li>
            </ol>
        </b-card>
        <b-card id="Setting" title="Setting">
            <p>
                You can enter porject setting place <br>
                From project title dropdown menu
            </p>
            <b-img fluid :src="ImageURL('img/ClientDocs/ProjectTitleContextMenuEN.png')" /> <br><br>
            <b-img fluid :src="ImageURL('img/ClientDocs/ProjectSettingEN.png')" /> <br><br>
            <ol>
                <li>Project: Project info</li>
                <li>Department: Department collection management</li>
                <li>Staff: Join memeber management</li>
                <li>Role: Permission collection management</li>
                <li>InviteCode: Join code management</li>
            </ol>
        </b-card>
        <b-card id="Schedule" title="Schedule">
            
        </b-card>
        <b-card id="Text" title="Text Channel">

        </b-card>
        <b-card id="File" title="File Channel">

        </b-card>
        <b-card id="Request" title="Request Channel">
            
        </b-card>
        <b-card id="Evaluation" title="Evaluation Channel">
            
        </b-card>
        <b-card id="Graph" title="Graph Channel">
            
        </b-card>
        <b-card id="Outsource" title="Outsource Channel">
            
        </b-card>
        <b-card id="Task" title="Task Channel">
            
        </b-card>
        <b-card id="Issue" title="Issue Channel">
            
        </b-card>
    </div>
</template>

<script>
import Base from './../../../Base'
import main from '../../../../main'
export default Base.extend({
    name: "ClientDocsProject",
    props:{
        lan: String
    },
    computed:{
        main(){
            return main;
        }
    },
})
</script>