<script>
import Base from '../../../Base'
import main from '../../../../main'
export default Base.extend({
    name: "ClientAPIBase",
    props:{
        lan: String
    },
    computed:{
        main(){
            return main;
        },
        copyBaseComponent(){
            return `import ComponentBase from "@/ComponentBase"`;
        },
        copySearchBar(){
            return `import Searchbar from "@/components/general/SearchBar/SearchBar.vue"`;
        },
        copyUserAvatar(){
