        },
        copyTopButton(){
            return `import TopButton from "@/components/general/TopButton.vue"`;
        },
        copyTopFilter(){
            return `import TopFilter from "@/components/general/TopFilter.vue"`;
        },
        copyTopDropdown(){
            return `import TopDropdown from "@/components/general/TopDropdown.vue"`;
        },
        copyPageSelector(){
            return `import PageSelector from "@/components/general/PageSelector.vue"`;
        },
        copyDialogTemp(){
            return `import DialogTemp from "@/components/general/Dialog.vue"`;
        },
        copyDatePicker(){
            return `import DatePicker from "@/components/general/dialog/selector/DatePicker/DatePicker.vue"`;
        },
        copyTimePicker(){
            return `import TimePicker from "@/components/general/dialog/selector/TimePicker/TimePicker.vue"`;
        },
        copyTiemSpanPicker(){
            return `import TimeSpanPicker from "@/components/general/dialog/selector/TimeSpanPicker/TimeSpanPicker.vue"`;
        },

        baseComponentFunction(){
            if(this.lan === "TW"){
                return [
                    { 名稱: "GetLanguageTags", 敘述: "從伺服器索取目前所有可用語言的標籤" },
                    { 名稱: "GetLanguageList", 敘述: "從伺服器獲得語言資料, 根據給予的標籤" },
                    { 名稱: "GetLanguage", 敘述: "從本地語言資料中搜尋字串, 根據給予的 Key" },
                    { 名稱: "GetDate", 敘述: "日期時間轉換成字串 => YYYY-MM-DD" },
                    { 名稱: "GetPreciseDate", 敘述: "日期時間轉換成字串 => YYYY-MM-DD:hh:mm:ss" },
                    { 名稱: "GetTime", 敘述: "時間長度轉換成字串 => dd:hh:mm:ss" },
                    { 名稱: "GetShortTime", 敘述: "時間長度轉換成字串 => hh:mm" },
                    { 名稱: "TimeGapSmall", 敘述: "給予時, 分, 秒, 得到時間長度" },
                    { 名稱: "TimeGapBig", 敘述: "給予年, 月, 日, 得到時間長度" },
                    { 名稱: "OpenURL", 敘述: "打開連結" },
                ]
            }else{
                return [
                    { Name: "GetLanguageTags", Description: "Get language tags from server" },
                    { Name: "GetLanguageList", Description: "Get language data from server according to input tag" },
                    { Name: "GetLanguage", Description: "Search language value by key" },
                    { Name: "GetDate", Description: "Date time to string => YYYY-MM-DD" },
                    { Name: "GetPreciseDate", Description: "Date time to string => YYYY-MM-DD:hh:mm:ss" },
                    { Name: "GetTime", Description: "Time duration to string => dd:hh:mm:ss" },
                    { Name: "GetShortTime", Description: "Time duration to string => hh:mm" },
                    { Name: "TimeGapSmall", Description: "Hour, Minute, Second to time duration" },
                    { Name: "TimeGapBig", Description: "Year, Month, Day to time duration" },
                    { Name: "OpenURL", Description: "Open URL" },
                ]
            }
        },
        searchBarProp(){
            if(this.lan === "TW"){
                return [
                    {名稱: "value", 資料型態: "String", 敘述: "搜尋的字串"},
                    {名稱: "usebutton", 資料型態: "Boolean", 敘述: "是否在右邊出現按鈕, 更新事件綁定在按鈕上"},
                ];
            }else{
                return [
                    {Name: "value", Type: "String", Description: "Search keyword"},
                    {Name: "usebutton", Type: "Boolean", Description: "Display button on the right and binding with update event"},
                ];
            }
        },
        searchBarEvent(){
            if(this.lan === "TW"){
                return [
                    {名稱: "search", 引數: "String", 敘述: "針對引數的字串進行搜尋的事件"},
                ];
            }else{
                return [
                    {Name: "search", Arugment: "String", Description: "Search by the text in arugment"},
                ];
            }
        },
        userAvatarProp(){
            if(this.lan === "TW"){
                return [
                    {名稱: "id", 資料型態: "String", 敘述: "用戶的GUID"},
