                    {名稱: "click", 引數: "MouseEvent", 敘述: "按鈕按下事件"},
                ];
            }else{
                return [
                    {Name: "click", Arugment: "MouseEvent", Description: "Button click event"},
                ]
            }
        },
        topFilterProp(){
            if(this.lan === "TW"){
                return [
                    {名稱: "prefix", 資料型態: "String", 敘述: "語言搜尋前綴"},
                    {名稱: "label", 資料型態: "String", 敘述: "標題文字"},
                    {名稱: "value", 資料型態: "String", 敘述: "選擇物件"},
                    {名稱: "items", 資料型態: "Array", 敘述: "選單元件"},
                    {名稱: "color", 資料型態: "String", 敘述: "元件顏色"},
                ];
            }else{
                return [
                    {Name: "prefix", Type: "String", Description: "Language search prefix"},
                    {Name: "label", Type: "String", Description: "title text"},
                    {Name: "value", Type: "String", Description: "Select object"},
                    {Name: "items", Type: "String", Description: "elements"},
                    {Name: "color", Type: "String", Description: "Component color"},
                ]
            }
        },
        topFilterEvent(){
            if(this.lan === "TW"){
                return [
                    {名稱: "change", 引數: "any", 敘述: "選擇物件更新"},
                ];
            }else{
                return [
                    {Name: "change", Arugment: "any", Description: "Select item updated"},
                ]
            }
        },
        topDropdownProp(){
            if(this.lan === "TW"){
                return [
                    {名稱: "prefix", 資料型態: "String", 敘述: "語言搜尋前綴"},
                    {名稱: "text", 資料型態: "String", 敘述: "按鈕文字"},
                    {名稱: "items", 資料型態: "Array", 敘述: "選單元件"},
                    {名稱: "icon", 資料型態: "String", 敘述: "按鈕 Icon"},
                    {名稱: "color", 資料型態: "String", 敘述: "按鈕顏色"},
                ];
            }else{
                return [
                    {Name: "prefix", Type: "String", Description: "Language search prefix"},
                    {Name: "text", Type: "String", Description: "Button text"},
                    {Name: "items", Type: "String", Description: "elements"},
                    {Name: "icon", Type: "String", Description: "Button icon"},
                    {Name: "color", Type: "String", Description: "Button color"},
                ]
            }
        },
        topDropdownEvent(){
            if(this.lan === "TW"){
                return [
                    {名稱: "click", 引數: "MouseEvent, Number", 敘述: "選擇事件, 回傳選擇陣列的目錄"},
                ];
            }else{
                return [
                    {Name: "click", Arugment: "MouseEvent, Number", Description: "Select event, Return select element index"},
                ]
            }
        },
        pageSelectorProp(){
            if(this.lan === "TW"){
                return [
                    {名稱: "page", 資料型態: "Number", 敘述: "目前頁面"},
                    {名稱: "length", 資料型態: "Number", 敘述: "總共頁面"},
                    {名稱: "total", 資料型態: "Number", 敘述: "元件最多顯示頁數"},
