            if(this.lan === "TW"){
                return [
                    {名稱: "change", 引數: "Number", 敘述: "選擇頁更新"},
                ];
            }else{
                return [
                    {Name: "change", Arugment: "Number", Description: "Select page update"},
                ]
            }
        },
        dialogTempProp(){
            if(this.lan === "TW"){
                return [
                    {名稱: "value", 資料型態: "Boolean", 敘述: "彈出視窗的值"},
                    {名稱: "color", 資料型態: "String", 敘述: "視窗背景顏色, 預設: 'backgroundPanel3'"},
                    {名稱: "loading", 資料型態: "Boolean", 敘述: "讀取條顯示"},
                    {名稱: "width", 資料型態: "String", 敘述: "視窗寬度"},
                    {名稱: "title", 資料型態: "String", 敘述: "標題文字"},
                ];
            }else{
                return [
                    {Name: "value", Type: "Boolean", Description: "Dialog value"},
                    {Name: "color", Type: "String", Description: "Dialog background color, Default: 'backgroundPanel3'"},
                    {Name: "loading", Type: "Boolean", Description: "Loading bar display"},
                    {Name: "width", Type: "String", Description: "Dialog width"},
                    {Name: "title", Type: "String", Description: "Dialog title text"},
                ]
            }
        },
