            if(this.lan === "TW"){
                return [
                    {名稱: "change", 引數: "MouseEvent, Boolean", 敘述: "視窗值更新"},
                ];
            }else{
                return [
                    {Name: "change", Arugment: "MouseEvent, Boolean", Description: "Dialog value update"},
