            if(this.lan === "TW"){
                return [
                    {名稱: "value", 資料型態: "Boolean", 敘述: "彈出視窗的值"},
                    {名稱: "min", 資料型態: "String", 敘述: "最小時間限制, xx:xx"},
                    {名稱: "max", 資料型態: "String", 敘述: "最大時間限制, xx:xx"},
                    {名稱: "title", 資料型態: "String", 敘述: "標題文字"},
