<template>
    <div v-if="lan=='TW'">
        <h2>首頁</h2>
            <dl>
                <dt>簡介</dt>
                    <dd><b-link @click="To('ClientIntro', 'Welcome')">- 簡介</b-link></dd>
                    <dd><b-link @click="To('ClientIntro', 'Download')">- 下載</b-link></dd>
            </dl>
        </div>
    <div v-else>
        <h2>Home</h2>
        <dl>
            <dt>Intro</dt>
                <dd><b-link @click="To('ClientIntro', 'Welcome')">- Intro</b-link></dd>
                <dd><b-link @click="To('ClientIntro', 'Download')">- Download</b-link></dd>
        </dl>
    </div>
</template>

<script>
import { bus } from '../../main';
import Base from '../Base'
export default Base.extend({
    name: "ClientIntro",
    props:{
        lan: String
    },
    created(){
        bus.$emit("ChangePage", "ClientIntro", "Welcome");
    },
    methods:{
        To(page, index){
            const topic = this.$route.params.topic;
            document.location.href = this.Tohref(topic, page, index);
        }
    }
})
</script>