<template>
    <div v-if="lan=='TW'">
        <h2>伺服器 簡介</h2>
        <b-card id="Welcome" title="需求">
            <p>
                後續的文章需要一個架設好的 Xanthus 伺服器 <br>
                如果你在架設伺服器遇到問題請至上一回的<b-link @click="ToSetup">架設步驟</b-link>
            </p>
        </b-card>

        <b-card id="Command" title="指令系統">
            <div>
                <p>
                    伺服器的指令模塊提供 CLI 環境 <br>
                    提供網路管理員能輸入指令與伺服器互動
                </p>
                <b-link @click="ToPage('Server-Docs-Command')">指令系統</b-link>
            </div>
        </b-card>

        <b-card id="Network" title="網路組件">
            <div>
                <p>
                    網路模組負責解決伺服器與客戶端之間的傳輸資料動作 <br>
                    伺服器的核心邏輯都在接收區的模塊
                </p>
                <b-link @click="ToPage('Server-Docs-Network')">網路組件</b-link>
            </div>
        </b-card>

        <b-card id="Database" title="資料庫端口">
            <div>
                <p>
                    資料庫的模組提供 CRUD 動作 (建立, 讀取, 更新, 刪除) <br>
                    在此之上提供對於特定資料庫的動作
                </p>
                <b-link @click="ToPage('Server-Docs-Database')">資料庫端口</b-link>
            </div>
        </b-card>

        <b-card id="Login" title="登入管理員帳號">
            <div>
                <p>
                    在伺服器建立的時候, 後台會偵測目前資料庫內是否有帳號, 職務, 組織架構存在 <br>
                    如果任意元件不存在會新增一個 <br>
                    在新增帳號時, 一定會是生成管理員帳號, 並套用管理員使用的職務跟組織架構
                </p>
                <p>以下為自動生成的管理員帳號</p>
                <p>用戶名: <span class="bg-light text-primary">root</span></p>
                <p>密碼: <span class="bg-light text-primary">12345</span></p>
                <p>
                    嘗試用客戶端登入
                </p>
                <b-img fluid src="/img/ServerDocs/LoginAdmin.png"></b-img>
                <b-img fluid src="/img/ServerDocs/AdminPermission.png"></b-img>
                <p>
                    你可以從個人頁面看到, 所有的管理頁面都是顯示的狀態 <br>
                    這代表你目前的帳號擁有觀測的權限 <br>
                    接著我們來資料庫的 xanthus/organ 查看
                </p>
                <b-img fluid src="/img/ServerDocs/OrganAdminPermission.png"></b-img>
                <p>
                    伺服器會固定創建一個組織圖的頂端 <br>
