                </p>
                <b-alert show variant="danger">
                    別試圖修改 op 的名稱或是權限架構 <br>
                    避免未來在作業時出現問題
                </b-alert>
            </div>
        </b-card>
    </div>
    <div v-else>
        <h2>Server Docs</h2>
        <b-card id="Welcome" title="Requirement">
            <p>
                The document will require you have an fully setup Xanthus server <br>
                If you have problem with setup server, please check <b-link @click="ToSetup">Setup step by step</b-link>
            </p>
        </b-card>

        <b-card id="Command" title="Command Module">
            <div>
                <p>
                    Command module provide a CLI environment <br>
                    Provide admin a way to interfact with server
                </p>
                <b-link @click="ToPage('Server-Docs-Command')">Command Module</b-link>
            </div>
        </b-card>

        <b-card id="Network" title="Network Module">
            <div>
                <p>
                    Network module solving for between client and server communication <br>
                    The core logic of server is all in receiver part of network module
                </p>
                <b-link @click="ToPage('Server-Docs-Network')">Network Module</b-link>
            </div>
        </b-card>

        <b-card id="Database" title="Database Module">
            <div>
                <p>
                    Database module provide CRUD action (Create, Read, Update, Delete) <br>
                    Add plus some feature for specific type of data collection
                </p>
                <b-link @click="ToPage('Server-Docs-Database')">Database Module</b-link>
            </div>
        </b-card>

        <b-card id="Login" title="Login Admin Account">
            <div>
                <p>
                    When server is created, background thread will check if database has any account, class, organ document exist <br>
                    If not it will create one for you <br>
                    And when create account, it will always be admin account, it will apply admin use class and organ
                </p>
                <p>Here is the automatically generate account</p>
                <p>Username: <span class="bg-light text-primary">root</span></p>
                <p>Password: <span class="bg-light text-primary">12345</span></p>
