                    Let's login with client
                </p>
                <b-img fluid src="/img/ServerDocs/LoginAdmin.png"></b-img>
                <b-img fluid src="/img/ServerDocs/AdminPermission.png"></b-img>
                <p>
                    As you can see in the profile page, all the management channel is display <br>
                    This means you have the permissions to check these channel <br>
                    Then let's check mongoDB at path: xanthus/organ
                </p>
                <b-img fluid src="/img/ServerDocs/OrganAdminPermission.png"></b-img>
                <p>
                    Server will created an fixed admin organ at top <br>
                    And this organ will enable all the permission
                </p>
                <b-alert show variant="danger">
                    Do not rename the op element's name or permission structure <br>
                    Prevent bug in the future
                </b-alert>
            </div>
        </b-card>
    </div>
</template>

<script>
import vue from 'vue'
import { bus } from '../../../../main'
export default vue.extend({
    name: "ServerDocs",
    props:{
        lan: String
    },
    methods:{
        ToSetup(){
            bus.$emit("ChangeTopic", "Server-Intro");
            setTimeout(() => {
                bus.$emit("ChangePage", "Server-Setup");
            }, 20);
        },
        ToPage(v){
            bus.$emit("ChangePage", v)
        }
    }
})
</script>