<template>
    <div v-if="lan=='TW'">
        <h2>客戶端 登入</h2>
        <b-card id="ReConnect" title="重新連接">
            <div>
                <p>
                    按下重新連線, 或是第一次開啟程式時 <br>
                    會出現這個視窗
                </p>
                <b-img fluid :src="ImageURL('img/ClientDocs/LoginConnection.png')" />
                <p>
                    輸入伺服器位址, 可以選擇是否輸入 port, 預設的 port 為 555
                </p>
                <p>
                    範例A: <span class="text-primary">192.168.0.105</span> => <span class="text-success bg-light">ws://192.168.0.105:555</span><br>
                    範例B: <span class="text-primary">192.168.0.105:65255</span> => <span class="text-success bg-light">ws://192.168.0.105:65255</span>
                </p>
            </div>
        </b-card>
        <b-card id="Info" title="訊息">
            <div>
                <p>
                    客戶端軟體的資訊會顯示在登入頁面
                </p>
                <b-img fluid :src="ImageURL('img/ClientDocs/LoginInfo.png')" />
            </div>
        </b-card>
        <b-card id="AutoUpdate" title="自動更新">
            <div>
                <p>
                    自動更新偵測, 開啟軟體後會啟動版本更新偵測 <br>
                    用戶可以選擇是否要更新 (強烈建議更新)
                </p>
                <ol>
                    <li>查看目前客戶端版本</li>
                    <li>查看目前最新客戶端版本</li>
                    <li>比較版本, 如果沒有匹配將會跳出更新視窗</li>
                </ol>
                <b-img fluid :src="ImageURL('img/ClientDocs/Update.png')" />
            </div>
        </b-card>
    </div>
    <div v-else>
        <h2>Client Login</h2>
        <b-card id="ReConnect" title="Re-Connect">
            <div>
                <p>
                    Client other connection, Or first time open program <br>
                    Will show this message box
                </p>
                <b-img fluid :src="ImageURL('img/ClientDocs/LoginConnection.png')" />
                <p>
                    Enter server address, port is optional, default port is 555
                </p>
                <p>
                    Ex.A: <span class="text-primary">192.168.0.105</span> => <span class="text-success bg-light">ws://192.168.0.105:555</span><br>
                    Ex.B: <span class="text-primary">192.168.0.105:65255</span> => <span class="text-success bg-light">ws://192.168.0.105:65255</span>
                </p>
            </div>
        </b-card>
        <b-card id="Info" title="Info">
            <div>
                <p>
                    Client software information will show at here
                </p>
                <b-img fluid :src="ImageURL('img/ClientDocs/LoginInfo.png')" />
            </div>
        </b-card>
        <b-card id="AutoUpdate" title="Auto Update">
            <div>
                <p>
                    Auto update detection, when open program <br>
                    User can have choose not to update (Recommand update for prevent bug)
                </p>
                <ol>
                    <li>Check current app version</li>
                    <li>Check latest client version</li>
                    <li>Comparison, Pop up auto update message when no match</li>
                </ol>
                <b-img fluid :src="ImageURL('img/ClientDocs/Update.png')" />
            </div>
        </b-card>
    </div>
</template>

<script>
import Base from '../../../Base'
import main from '../../../../main'
export default Base.extend({
    name: "ClientDocs",
    props:{
        lan: String
    },
    computed:{
        main(){
            return main;
        }
    },
})
</script>