/* eslint-disable no-unused-vars */
import Base from './components/Base'
import Main from './components/Main.vue'
import Nav from './components/Nav.vue'
import Sidebar from './components/Sidebar.vue'
import { bus } from './main';


export default Base.extend({
  name: 'Page',
  components: {
    Main, Nav, Sidebar
  },
  created(){
    window.onhashchange = (event) => {
      const old = event.oldURL;
      const neww = event.newURL;
      const aold = this.AnalysisURL(old);
      const anew = this.AnalysisURL(neww);
      console.log(aold, anew);
      if(aold.length === 3 && anew.length === 3 && aold[0] == anew[0] && aold[1] == anew[1]){
        bus.$emit("ChangeHash", anew[2]);
      }else{
        document.location.reload();
      }
    }
    bus.$on("ChangeLan", this.Lan);
  },
  destroyed(){
    bus.$off("ChangeLan", this.Lan);
  },
  mounted(){
    const topic = this.$route.params.topic;
    const page = this.$route.params.page;
    const index = this.$route.params.index;
    console.log("topic && page", topic, page);
    if(page != undefined){
      this.changeTP(topic, page, "");
    }else{
      this.changeTopic(topic);
    }

    const lan = localStorage.getItem("lan")
    console.log("Lan", lan);
    if(lan != undefined) this.Lan(lan);
    else{
      localStorage.setItem("lan", "TW");
      this.Lan("TW");
    }


    console.log("hash", index)
    if(index != undefined){
      bus.$emit("ChangeHash", index);
    }
  },
  methods:{
    Lan(tag){
      localStorage.setItem("lan", tag);
      this.lan = tag;
    },
  },
  data(){
    return {
      lan: "TW"
    }
  }
})