import vue from 'vue'
import main, { bus } from '../main';

export default vue.extend({
    computed:{
        main(){
            return main;
        }
    },
    methods: {
        changeLan(tag){
            console.log("Change to: " + tag);
            bus.$emit("ChangeLan", tag);
        },
        changeTopic(v){
            bus.$emit("ChangeTopic", "");
            setTimeout(() => {
                bus.$emit("ChangeTopic", v);
            }, 20);
        },
        changePage(v, v2){
            bus.$emit("ChangePage", v, v2)
        },
        changeTP(v, v2, v3){
            bus.$emit("ChangeTopic", "");
            setTimeout(() => {
                bus.$emit("ChangeTopic", v);
                setTimeout(() => {
                    this.changePage(v2, v3);
                }, 20);
            }, 20);
        },
        copy(v){
            document.execCommand('copy', false, v);
        },
        Alert(v){
            alert(v)
        },
        To(topic, page, index){
            document.location.href = this.Tohref(topic, page, index)
        },
        Tohref(topic, page, index){
            if(topic && page && index)
                return `${this.FrontURL()}/#/${topic}/${page}/${index}`;
            else if(topic && page && !index)
                return `${this.FrontURL()}/#/${topic}/${page}`;
            else if(topic && !page && !index)
                return `${this.FrontURL()}/#/${topic}`;
        },
        FrontURL(){
            if(process.env.NODE_ENV === 'development'){
                return `${document.location.protocol}//${document.location.host}`
            }else{
                return `${document.location.protocol}//${document.location.host}/XanthusDocs`
            }
        },
        ImageURL(url){
            if(process.env.NODE_ENV === 'development'){
                return `/${url}`
            }else{
                return `/XanthusDocs/${url}`
            }
        },
        AnalysisURL(url){
            return url.replace(this.FrontURL() + "/#/", '').split('/');
        }
    }
})