<template>
    <div v-if="lan=='TW'">
        <h2>伺服器 備份 文件</h2>
        <b-card id="Tool" title="安裝 Xanthus 資料庫工具">
            <div>
                <p>
                    進入 MongoDB 網站下載 CLI 工具 <br>
                    這些工具內含有備份和復原的工具
                </p>
                <b-link src="https://www.mongodb.com/try/download/database-tools">MongoDB 工具</b-link>
            </div>
        </b-card>

        <b-card id="Var" title="設置環境變數">
            <div>
                <p>進入系統設定</p>
                <b-img fluid src="/img/ServerDocs/SystemProp.png" />
                <p>環境變數中的 Path 選項</p>
                <b-img fluid src="/img/ServerDocs/SystemPropPath.png" />
                <p>
                    新增目前你目前 MongoDB 環境的路徑 <br>
                    C:\Program Files\MongoDB\Server\[你的MongoDB版本]\bin <br>
                    C:\Program Files\MongoDB\Tools\100\bin
                </p>
                <b-img fluid src="/img/ServerDocs/EnvironmentVar.png" />
            </div>
        </b-card>

        <b-card id="Backup" title="備份開始">
            <div>
                <p>
                    打開 CMD 打入備份指令 <br>
                    在這之前請選擇你備份資料的檔案路徑
                </p>
                <p>選擇路徑: <span class="bg-light text-primary">cd [路徑]</span></p>
                <p>備份: <span class="bg-light text-primary">mongodump</span></p>
                <b-img fluid src="/img/ServerDocs/MongoDump.png" /> <br> <br>
                <b-img fluid src="/img/ServerDocs/MongoDumpFinish.png" />
                <p>
                    結束後會在指定的路徑出現 ./dump <br>
                    這個會以 Bson 的方式保存你的資料
                </p>
                <b-img fluid src="/img/ServerDocs/MongoDumpFile.png" />
            </div>
        </b-card>

        <b-card id="Restore" title="存取備份">
            <div>
                <p>
                    打開 CMD 打入備份指令 <br>
                    在這之前請選擇你備份資料的檔案路徑
                </p>
                <p>選擇路徑: <span class="bg-light text-primary">cd [路徑]</span></p>
                <p>復原: <span class="bg-light text-primary">mongorestore</span></p>
                <b-img fluid src="/img/ServerDocs/MongoRestore.png" /> <br> <br>
                <b-img fluid src="/img/ServerDocs/MongoRestoreFinish.png" />
                <p>
                    復原完成
                </p>
            </div>
        </b-card>

        <b-card id="Media" title="伺服器媒體資料">
            <div>
                <p>
                    Xanthus 伺服器除了透過 MongoDB 保存資料 <br>
                    另外還有媒體資料, 像是圖片或是檔案 <br>
                    進入伺服器資料夾
                </p>
                <b-img fluid src="/img/ServerDocs/ServerFolder.png" /> <br> <br>
                <b-img fluid src="/img/ServerDocs/MediaFolder.png" />
                <p>
                    這邊的資料也一併壓縮備份才算是完成
                </p>
            </div>
        </b-card>
    </div>
    <div v-else>
        <h2>Server Docs Backup</h2>
        <b-card id="Tool" title="Install Xanthus Database Tool">
            <div>
                <p>
                    Enter MongoDB Website install CLI Database tool <br>
                    These tool include backup and restore feature
                </p>
                <b-link src="https://www.mongodb.com/try/download/database-tools">MongoDB Tool</b-link>
            </div>
        </b-card>

        <b-card id="Var" title="Environemnt Variable">
            <div>
                <p>Enter system setting</p>
                <b-img fluid src="/img/ServerDocs/SystemProp.png" />
                <p>Environment variable 'Path' option</p>
                <b-img fluid src="/img/ServerDocs/SystemPropPath.png" />
                <p>
                    Add a new path to target your MongoDB environment <br>
                    C:\Program Files\MongoDB\Server\[MongoDB_Version]\bin <br>
                    C:\Program Files\MongoDB\Tools\100\bin
                </p>
                <b-img fluid src="/img/ServerDocs/EnvironmentVar.png" />
            </div>
        </b-card>

        <b-card id="Backup" title="Start Backup">
            <div>
                <p>
                    Open CMD Enter backup command <br>
                    Before that you should choose the path that where does the backup files store
                </p>
                <p>Choose path: <span class="bg-light text-primary">cd [Path]</span></p>
                <p>Backup: <span class="bg-light text-primary">mongodump</span></p>
                <b-img fluid src="/img/ServerDocs/MongoDump.png" /> <br> <br>
                <b-img fluid src="/img/ServerDocs/MongoDumpFinish.png" />
                <p>
                    When finished it will create ./dump folder <br>
                    Bakeup will store your data by convert them to bson
                </p>
                <b-img fluid src="/img/ServerDocs/MongoDumpFile.png" />
            </div>
        </b-card>

        <b-card id="Restore" title="Restore Backup">
            <div>
                <p>
                    Open CMD Enter backup command <br>
                    Before that you should choose the path that where does the backup files store
                </p>
                <p>Choose path: <span class="bg-light text-primary">cd [Path]</span></p>
                <p>Restore: <span class="bg-light text-primary">mongorestore</span></p>
                <b-img fluid src="/img/ServerDocs/MongoRestore.png" /> <br> <br>
                <b-img fluid src="/img/ServerDocs/MongoRestoreFinish.png" />
                <p>
                    Restore finish
                </p>
            </div>
        </b-card>

        <b-card id="Media" title="Server Media Files">
            <div>
                <p>
                    Xanthus Server not only store data on MongoDB database <br>
                    There are other type of data like picture or files <br>
                    Enter your server root folder
                </p>
                <b-img fluid src="/img/ServerDocs/ServerFolder.png" /> <br> <br>
                <b-img fluid src="/img/ServerDocs/MediaFolder.png" />
                <p>
                    It's finish when you backup these files as well
                </p>
            </div>
        </b-card>
    </div>
</template>

<script>
import vue from 'vue'
export default vue.extend({
    name: "ServerDocsBackup",
    props:{
        lan: String
    }
})
</script>